<template>
  <div class="comment" v-if="currentBook">
    <van-nav-bar left-arrow @click-left="onClickLeft" :fixed="true">
      <template #title>
        <img :src="`http://statics.zhuishushenqi.com${currentBook.cover}`" />
      </template>
      <template #right>
        <van-icon name="home-o" @click="$router.push({name:'Store'})" />
      </template>
    </van-nav-bar>
    <div class="word">
      <div class="rating">
        <div class="left">
          <div class="score">
            <span>{{ currentBook.rating.score.toFixed(1) }}</span>
            <van-rate
              v-model="star"
              allow-half
              :size="14"
              color="#ffd21e"
              void-icon="star"
              void-color="#eee"
              readonly
            />
          </div>
          <div class="tip">
            {{ currentBook.rating.tip }}
          </div>
        </div>
        <div class="right">
          <van-progress
            color="#666"
            :show-pivot="false"
            v-for="(item, index) in sRatings"
            :key="index"
            :percentage="(item.count / currentBook.rating.count) * 100"
          />
        </div>
      </div>
      <van-divider
        :style="{
          borderColor: 'rgb(240, 240, 240)',
          margin: 0,
        }"
      />
      <div class="main">
        <van-tabs v-model="active">
          <van-tab title="书评">
            <div class="commentbox">
              <div
                class="item"
                v-for="(item, index) in longComment"
                :key="index"
              >
                <div class="info">
                  <div class="avatar">
                    <img
                      :src="`http://statics.zhuishushenqi.com${item.author.avatar}`"
                    />
                  </div>
                  <div class="nickname">{{ item.author.nickname }}</div>
                  <div class="level">Lv.{{ item.author.lv }}</div>
                </div>
                <div class="star">
                  <van-rate
                    v-model="item.rating"
                    :size="14"
                    color="#ffd21e"
                    void-icon="star"
                    void-color="#eee"
                    readonly
                  />
                </div>
                <div class="tit">{{ item.title }}</div>
                <div class="content">{{ item.content }}</div>
                <div class="bottom">
                  <div class="left">
                    <span class="dt">{{ item.updated.split(/[T\.]/)[0] }}</span>
                    <span class="time">{{
                      item.updated.split(/[T\.]/)[1]
                    }}</span>
                  </div>
                  <div class="right">
                    <van-icon name="good-job-o" />
                    <div class="likecount">{{ item.likeCount }}</div>
                  </div>
                </div>
              </div>
            </div>
          </van-tab>
          <van-tab title="短评">
              <div class="commentbox">
              <div
                class="item"
                v-for="(item, index) in shortComment"
                :key="index"
              >
                <div class="info">
                  <div class="avatar">
                    <img
                      :src="`http://statics.zhuishushenqi.com${item.author.avatar}`"
                    />
                  </div>
                  <div class="nickname">{{ item.author.nickname }}</div>
                  <div class="level">Lv.{{ item.author.lv }}</div>
                </div>
                <div class="star">
                  <van-rate
                    v-model="item.rating"
                    :size="14"
                    color="#ffd21e"
                    void-icon="star"
                    void-color="#eee"
                    readonly
                  />
                </div>
                <div class="content">{{ item.content }}</div>
                <div class="bottom">
                  <div class="left">
                    <span class="dt">{{ item.updated.split(/[T\.]/)[0] }}</span>
                    <span class="time">{{
                      item.updated.split(/[T\.]/)[1]
                    }}</span>
                  </div>
                  <div class="right">
                    <van-icon name="good-job-o" />
                    <div class="likecount">{{ item.likeCount }}</div>
                  </div>
                </div>
              </div>
            </div>
          </van-tab>
        </van-tabs>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "Comment",
  props: ["shortComment", "longComment"],
  data() {
    return {
      active: 0,
    };
  },
  created() {
    this.$emit("update:showFooter", false);

    if (!this.currentBook) {
      this.axios.get(`/book-info/${this.$route.query.id}`).then((response) => {
        console.log(response);
        this.$store.commit("updateCurrentBook", response.data);
      });
    }
  },
  computed: {
    ...mapState(["currentBook"]),
    star() {
      var s = this.currentBook.rating.score / 2;
      var num = 0;
      switch (s) {
        case 4 < s < 5:
          num = 4.5;
          break;
        case 3 < s < 4:
          num = 3.5;
          break;
        case 2 < s < 3:
          num = 2.5;
          break;
        case 1 < s < 2:
          num = 1.5;
          break;
        case 0 < s < 1:
          num = 0.5;
          break;

        default:
          num = this.currentBook.rating.score / 2;
          break;
      }
      return num;
    },
    sRatings() {
      var re = this.currentBook.starRatings;
      return re.reverse();
    },
  },
  beforeRouteLeave(to, from, next) {
    this.$emit("update:showFooter", true);
    next();
  },
  methods: {
    onClickLeft() {
      this.$router.back();
    },
  },
};
</script>

<style lang="less" scoped>
.comment {
  /deep/ .van-nav-bar {
    height: 150px;
    background-color: #f1867a;
    .van-nav-bar__content {
      .van-icon {
        color: #000;
        font-size: 22px;
      }
      .van-nav-bar__title {
        width: 90px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%);
        img {
          width: 100%;
          display: block;
        }
      }
    }
  }
  .word {
    position: relative;
    z-index: 100;
    transform: translateY(-18px);
    .van-divider {
      padding: 3px 0;
      background-color: rgb(240, 240, 240);
    }
    .rating {
      display: flex;
      align-items: center;
      background-color: rgba(255, 255, 255, 0.8);
      margin-top: 150px;
      padding: 20px 0 20px 0;
      border-radius: 22px 22px 0 0;
      .left {
        flex-basis: 60%;
        padding-left: 25px;
        span {
          font-size: 25px;
          font-weight: bold;
          margin-right: 8px;
        }
        .tip {
          font-size: 12px;
          margin-top: 2px;
        }
      }
      .right {
        flex-basis: 35%;
        .van-progress {
          margin-top: 5px;
          width: 100%;
        }
      }
    }
    .main {
      background-color: #fff;
      .van-tabs {
        height: 34px;
      }
      .commentbox {
        height: calc(100vh - 278px);
        overflow-y: scroll;
        overflow-x: hidden;
        .item {
          margin: 0 15px;
          padding-bottom: 40px;
          position: relative;
          margin-bottom: 10px;
          .van-divider {
            position: absolute;
            width: 100%;
            bottom: 0;
          }
          .info {
            margin-top: 10px;
            display: flex;
            align-items: center;
            .avatar {
              width: 34px;
              height: 34px;
              border-radius: 50%;
              overflow: hidden;
              img {
                display: block;
                width: 100%;
              }
            }
            .nickname {
              font-size: 14px;
              margin: 0 10px;
              color: #555;
            }
            .level {
              font-size: 12px;
              padding: 0 5px;
              color: #777;
              border: 1px solid #bbb;
              border-radius: 8px;
            }
          }
          .star {
            margin: 6px 0;
            display: flex;
            align-items: center;
          }
          .tit {
            font-size: 14px;
            padding-right: 6px;
            font-weight: bold;
            margin-bottom: 5px;
          }
          .content {
            font-size: 13px;
            padding-right: 6px;
            color: #666;
          }
          .bottom {
            position: absolute;
            width: 100%;
            bottom: 6px;
            font-size: 13px;
            color: #888;
            display: flex;
            .left {
              span {
                margin-right: 4px;
              }
            }
            .right {
              position: absolute;
              right: 10px;
              display: flex;
              .likecount {
                margin: 0 2px;
              }
            }
          }
        }
      }
    }
  }
}
</style>